<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <!-- 头部搜索 -->
      <el-row>
        <table-com
          :ref="refName"
          :refresh-key="refreshKey"
          :table-height-type="490"
          :loading="loading"
          :index="true"
          :index-fixed="false"
          :selection="true"
          :multiple="multiple"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="tableData"
          :column-list="columnList"
          :show-operate="showOperate"
          :operate-fixed="false"
          :table-selection="tableSelection"
          :operation-width="120"
          @selectionChange="selectionChange"
        >
          <template slot-scope="scope">
            <el-button type="primary" :disabled="scope.row.isCanSplit === 0" @click="handleDevanning(scope.row)">拆箱</el-button>
          </template>
        </table-com>
      </el-row>
      <!-- <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" /> -->
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
    <devanning-dialog v-model="devanningvisible" :extra-info="devanningForm" @confirm="handleConfirmDevanning" />
  </el-dialog>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import DevanningDialog from './Devanning'
import paginationPage from '@/mixins/paginationPage'
import { getBarcodeListApi as getListSelectApi } from '@/apis/modules/warehouse/inventory'

export default {
  name: 'MaterialSelect',
  components: {
    DevanningDialog
  },
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '条码选择'
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => {}
    },
    showOperate: {
      type: Boolean,
      default: true
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialSelect',
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码号',
          align: 'center',
          minWidth: 170,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'currentPosition',
          label: '存放位置',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      devanningForm: {}, // 拆箱弹窗表单详情
      devanningvisible: false, // 拆箱弹窗变量
      refreshKey: 1
    }
  },
  methods: {
    confirmDialog() {
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    handleConfirmDevanning(item) {
      this.$emit('syncBarcode', item)
      this.init().then(() => {
        // 选中条码信息同步
        const clickItem = this.tableData.find(c => c.id === this.devanningForm.id)
        clickItem && (this.devanningForm = clickItem)
      })
    },
    init() {
      const params = {
        ...this.query,
        // materialIdList: this.ids,
        ...this.extraInfo
        // page: {
        //   ...this.pageInfo
        // }
      }
      return new Promise((resolve, reject) => {
        getListSelectApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // const { list, ...pageInfo } = data
            // this.initPage(pageInfo)
            this.tableData = data.map(i => {
              return {
                uuid: uuidv4(),
                deliveryNumber: i.orderNum,
                ...i
              }
            })
            // 初始化selection
            this.refreshKey++
            this.tableSelection = this.tableData.filter(i => this.selection.includes(i.id))
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 更新列表复选框勾选
    handleUpdateTableSelected(type = '') {

    },
    handleDefault() {
      this.tableSelection = new Array(0)
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.handleDefault()
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    },
    handleDevanning(item) {
      this.devanningvisible = true
      this.devanningForm = { ...item }
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
